<template>
  <div class="min-h-screen bg-gray-50">
    <div class="container mx-auto p-6">
      <!-- Header -->
      <div class="flex items-center justify-between mb-6">
        <h1 class="text-2xl font-semibold text-gray-800">Products</h1>
        <button
          @click="addNew"
          class="bg-blue-600 text-white px-4 py-2 rounded hover:bg-blue-700"
        >
          Add New
        </button>
      </div>

      <!-- Table -->
      <div class="overflow-x-auto rounded-lg shadow-sm">
        <table class="min-w-full divide-y divide-gray-200">
          <thead class="bg-gray-200">
            <tr>
              <th class="px-4 py-2 text-left">ID</th>
              <th class="px-4 py-2 text-left">Name</th>
              <th class="px-4 py-2 text-left">Code</th>
              <th class="px-4 py-2 text-left">Category</th>
              <th class="px-4 py-2 text-left">Serial No</th>
              <th class="px-4 py-2 text-left">Unit</th>
              <th class="px-4 py-2 text-left">Purchase Price</th>
              <th class="px-4 py-2 text-left">Sales Price</th>
              <th class="px-4 py-2 text-left">MRP</th>
              <th class="px-4 py-2 text-left">Initial Stock</th>
              <th class="px-4 py-2 text-left">MFG Date</th>
              <th class="px-4 py-2 text-left">EXP Date</th>
              <th class="px-4 py-2 text-left">Active</th>
              <th class="px-4 py-2 text-center">Actions</th>
            </tr>
          </thead>
          <tbody>
            <tr
              v-for="(product, index) in products"
              :key="product.uuid"
              :class="index % 2 === 0 ? 'bg-white' : 'bg-gray-50'"
            >
              <td class="px-4 py-2">{{ product.id }}</td>
              <td class="px-4 py-2">{{ product.name }}</td>
              <td class="px-4 py-2">{{ product.code }}</td>
              <td class="px-4 py-2">{{ product.category_uuid }}</td>
              <td class="px-4 py-2">{{ product.serial_no }}</td>
              <td class="px-4 py-2">{{ product.unit }}</td>
              <td class="px-4 py-2">{{ product.purchase_price }}</td>
              <td class="px-4 py-2">{{ product.sales_price }}</td>
              <td class="px-4 py-2">{{ product.mrp }}</td>
              <td class="px-4 py-2">{{ product.initial_stock }}</td>
              <td class="px-4 py-2">{{ product.mfg_date }}</td>
              <td class="px-4 py-2">{{ product.exp_date }}</td>
              <td class="px-4 py-2">{{ product.is_active ? "Yes" : "No" }}</td>
              <td class="px-4 py-2 flex justify-center gap-2">
                <button
                  @click="editProduct(product.uuid)"
                  class="bg-yellow-400 px-3 py-1 rounded hover:bg-yellow-500 text-white"
                >
                  Edit
                </button>
                <button
                  @click="deleteProduct(product)"
                  class="bg-red-500 px-3 py-1 rounded hover:bg-red-600 text-white"
                >
                  Delete
                </button>
              </td>
            </tr>
            <tr v-if="products.length === 0" class="bg-white">
              <td colspan="18" class="text-center py-4 text-gray-500">
                No products found.
              </td>
            </tr>
          </tbody>
        </table>
      </div>
      <div class="pagination">
          <button @click="prevPage" :disabled="page === 1">Previous</button>
          <span>Page {{ page }} of {{ totalPages }}</span>
          <button @click="nextPage" :disabled="page === totalPages">Next</button>
        </div>
        <div v-if="products.value?.length === 0" class="text-center text-gray-500 mt-6">
          No categories found.
        </div>
    </div>
  </div>
</template>

<script setup>

import { productApiService } from "@/service/product/product.api.service";
import { productLocalService } from "@/service/product/product.local.service";
import { useChatStore } from "@/stores/socket/chat-store";
import { ref, onMounted, onUnmounted, computed, watch } from "vue";

const products = ref([]);
const loading = ref(false);
const page = ref(1)
const limit = ref(1)
const totalPages = ref(1)
const isSubmitStatus = ref(0)

const fetchAll = async () => {
  loading.value = true;
  try {
    const res = await productApiService.getAll(page.value, limit.value);
    totalPages.value = res.total || 1
    products.value = res.data || [];
  } catch (err) {
    console.error("API error:", err);
    products.value = [];
  } finally {
    loading.value = false;
  }
};

// Pagination functions
const nextPage = () => {
  if (page.value < totalPages.value) page.value++
}
const prevPage = () => {
  if (page.value > 1) page.value--
}

onMounted(async () => {
  await fetchAll();
  // chatStore.on("category", handleGlobalMessage);
});

</script>

<style scoped>
.pagination {
  margin-top: 20px;
  display: flex;
  gap: 10px;
  align-items: center;
}

table th,
table td {
  text-align: left;
}
</style>
